import { createApp } from 'vue'; // Vue 3 导入方式
import App from './App.vue';  // 导入根组件
import ElementPlus from 'element-plus'; // 导入 Element Plus
import 'element-plus/dist/index.css';  // 导入 Element Plus 的样式

const app = createApp(App);

// 使用 Element Plus
app.use(ElementPlus);

// 挂载 Vue 应用到 #app 元素
app.mount('#app');
