<template>
  <div id="app">
    <!-- 警告框组件 -->
    <el-alert
      v-if="alertVisible"
      title="提示"
      type="success"
      description="你成功提交了内容！"
      closable
      @close="handleAlertClose"
      style="margin-bottom: 20px;"
    />
    
    <!-- 输入框组件 -->
    <el-input
      v-model="inputMessage"
      placeholder="请输入一些内容"
      clearable
      style="width: 300px; margin-bottom: 20px;"
    />
    
    <!-- 提交按钮组件 -->
    <el-button
      type="primary"
      :disabled="inputMessage.trim() === ''"
      @click="handleSubmit"
    >
      提交
    </el-button>

    <!-- 清空按钮组件 -->
    <el-button
      @click="clearInput"
      style="margin-left: 10px"
    >
      清空
    </el-button>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      inputMessage: '',  // 存储输入框的值
      alertVisible: false,  // 控制警告框的显示与否
    };
  },
  methods: {
    // 提交按钮的处理方法
    handleSubmit() {
      // 显示警告框
      this.alertVisible = true;
      console.log("用户提交的消息:", this.inputMessage);
    },
    
    // 关闭警告框
    handleAlertClose() {
      this.alertVisible = false;
    },
    
    // 清空输入框内容
    clearInput() {
      this.inputMessage = '';
    }
  }
};
</script>

<style>
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}
</style>
